body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  margin-right: 1em;
}

#dropZone {
  height: 18rem;
  width: 18rem;
  padding: 3rem;

  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;

  background-color: #efefef;
  border-radius: 0.5rem;
}

#dropZone > .pointer {
  text-align: center;
  padding: 0.5rem;
  width: 100%;
}
#dropZone > .pointer > svg {
  font-size: 3rem;
  transform: rotate(-20deg);
}

#dropZone p {
  text-align: center;
}
#dropZone button {
  width: 100%;
}

#dropZone .inputfile {
  width: 0.01px;
  height: 0.01px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

#dropZone button > label {
  font-size: 1.25rem;
  font-weight: 700;
  color: white;
  display: inline-block;
  padding: 0.5rem;
  border-radius: 0.5rem;
  text-align: center;
  margin: 0;
}

#dropZone .icon-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 1rem;
}
#dropZone .icon-list > * {
  padding: 0.5rem;
}

.data-container {
  display: flex;
  flex-direction: row;
  border: solid 2px #aaaaaa;
  border-radius: 0.5rem;
}
.title-text-container {
  width: calc(100% - 18rem);
  display: flex;
  flex-direction: column;
  border: solid 1px #aaaaaa;
  border-width: 0 1px 0 0;
  padding: 1rem;
}
.title-text-container > textarea {
  margin-top: 0.5rem;
  padding: 1rem;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  flex-grow: 8;
}

.checkbox {
  visibility: hidden;
}
.checkbox-container {
  cursor: pointer;
}
.checkbox-bar {
  width: 30px;
  height: 10px;
  background: #555;
  margin: 10px 15px;
  position: relative;
  border-radius: 3px;
}
.checkbox-label {
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 50%;

  transition: all .2s ease;
  position: absolute;
  top: -3px;
  left: -3px;

  background: #ccc;
}
.checkbox:checked + label {
  left: 17px;
}

.ipsum-salt {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.5rem;
}
.ipsum-salt > p {
  text-transform: uppercase;
  font-weight: bold;
  color: rgba(0,0,0,0.5);
  flex-basis: 10%;
  line-height: calc(2.25rem + 2px);
  margin: 0;
}
.ipsum-salt > select {
  flex-basis: 65%;
}
.ipsum-salt > .icon {
  flex-basis: 2rem;
  text-align: right;
}
.ipsum-salt > button {
  align-self: flex-end;
  margin: 0 0.5rem 0 1rem;
  flex-basis: 18.5rem;
}

.color-text {
  fill: white;
  stroke: #000;
  stroke-width: 1;
  stroke-linejoin: round;
  paint-order: stroke;
  font-weight: bold;
  font-size: 65%;
  stroke-opacity: 0.3;
}
.trail-text-name {
  font-size: 0.8rem;
  text-transform: lowercase;
  color: rgba(0,0,0,0.2);
  margin: 0.2rem;
}

.acid-trail-controls {
  display: flex;
  flex-direction: row;
}
.choices-container {
  flex-basis: 80%;
}
.showChoice {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.showChoice > p {
  flex-basis: 15%;
  margin: 0 0 8px;
  line-height: 32px;
  text-transform: uppercase;
  font-weight: bold;
  color: rgba(0,0,0,0.5);
}
.showChoice > label {
  flex-basis: 15%;
  flex-grow: 0;
  flex-shrink: 0;
}
.buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  flex-basis: 20%;
}
.showChoice-container {
  flex-basis: 20%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.acid-trail {
  padding: 0.3rem;
}
.acid-trail svg + svg {
  margin-top: 0.3rem;
}
.trail-reference {
  display: flex;
  flex-direction: row;
}
.trail-reference > span {
  padding-left: 0.5rem;
  font-family: monospace;
  overflow-wrap: break-word;
}

.acid-trail-charts {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 0.3rem;
  background-color: rgba(255,255,255,0.8);
  box-shadow: inset 1px 1px 2px 1px rgba(0,0,0,0.4);
}

